:root {
   --gradientY: linear-gradient(180deg, #58F597 0%, rgba(88, 170, 245, 0.85) 100%);
   --gradientX: linear-gradient(89.95deg, #58F597 0.02%, rgba(88, 170, 245, 0.85) 99.98%);
   --light-green: #58f597;
   --light-gray: #939393;

   --content--max-width: 1195px;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
   outline: none;
}
.unselectable {
   -webkit-user-select: none;  /* Chrome all / Safari all */
   -moz-user-select: none;     /* Firefox all */
   -ms-user-select: none;      /* IE 10+ */
   user-select: none;          /* Likely future */
}
[aria-valuetext]::after {
   content: attr(aria-valuetext);
}
.cursor-pointer {
   cursor: pointer !important;
}
.modal-content {
