/* Checkbox */
.content-checkbox{
   -webkit-appearance: none;
   background-color: white;
   border: solid 2px rgba(88, 170, 245, 0.85);
   border-radius: 5px;
   padding: 10px !important;
   display: inline-block;
   position: relative;
   cursor: pointer;
}

.content-checkbox:checked {
   background: rgba(88, 170, 245, 0.85);
   background: url('data:image/svg+xml;base64,') 50% 50% no-repeat;
   background-size: 100%;
}
.transition-load {
