.header-popover-container {
    display: none;
    position: absolute;
    top: 80px;
    left: 60%;
    transform: translate(-50%, 0);
    min-width: 300px;
    background: white;
    border-radius: 0 0 20px 20px;
    z-index: 500;
    padding: 25px 35px;
    flex-flow: row wrap;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.12);
